// @mui
import { Container, Typography, Card, Box, Stack, Alert, Chip } from '@mui/material';
// routes
import { PATH_DASHBOARD } from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
import useAuth from '../../hooks/useAuth';
// components
import Page from '../../components/Page';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
import { CodeBlock, EndpointCard, ParameterTable } from '../../sections/docs';
import { HOST_API } from '../../config';

// ----------------------------------------------------------------------

export default function DocsAirtime() {
  const { themeStretch } = useSettings();
  const { setting, user } = useAuth();

  const requestCode = `$ch = curl_init();
curl_setopt($ch, CURLOPT_URL, "${HOST_API}/api/topup");
curl_setopt($ch, CURLOPT_POST, 1);
curl_setopt($ch, CURLOPT_POSTFIELDS, http_build_query([
    'network' => 1,
    'amount' => 100,
    'mobile_number' => '08011111111',
    'Ported_number' => true,
    'airtime_type' => 'VTU'
]));
curl_setopt($ch, CURLOPT_RETURNTRANSFER, 1);
curl_setopt(
    $ch, CURLOPT_HTTPHEADER, [
        "Authorization: Bearer {$user?.apikey}",
        "Content-Type: application/x-www-form-urlencoded"
    ]
);
$response = curl_exec($ch);
curl_close($ch);`;

  const successResponse = `{
    "status": "success",
    "message": "Airtime purchase successful",
    "data": {
        "network": "MTN",
        "amount": "100.00",
        "phone": "08011111111",
        "balance_before": "5000.00",
        "balance_after": "4900.00",
        "reference": "habukhan-dev-${Date.now()}"
    }
}`;

  const parameters = [
    {
      name: 'network',
      type: 'integer',
      required: true,
      description: 'Network ID (1=MTN, 2=GLO, 3=AIRTEL, 4=9MOBILE)',
    },
    {
      name: 'amount',
      type: 'integer',
      required: true,
      description: 'Airtime amount (minimum: 50, maximum: 50000)',
    },
    {
      name: 'mobile_number',
      type: 'string',
      required: true,
      description: 'Recipient phone number (11 digits)',
    },
    {
      name: 'Ported_number',
      type: 'boolean',
      required: true,
      description: 'Set to true if number is ported, false otherwise',
    },
    {
      name: 'airtime_type',
      type: 'string',
      required: true,
      description: 'Airtime type: VTU (Virtual Top-Up) or Share and Sell',
    },
  ];

  return (
    <Page title="Airtime Purchase API">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading="Airtime Purchase API"
          links={[
            { name: 'Dashboard', href: PATH_DASHBOARD.root },
            { name: 'Documentation', href: '/documentation/home' },
            { name: 'Airtime' },
          ]}
        />

        {/* Overview */}
        <Card sx={{ p: 4, mb: 3 }}>
          <Stack direction="row" alignItems="center" spacing={2} sx={{ mb: 2 }}>
            <Typography variant="h4">ðŸ“± Airtime Purchase</Typography>
            <Chip label="POST" color="success" size="small" />
          </Stack>
          <Typography variant="body1" color="text.secondary">
            Purchase airtime for any Nigerian network with instant delivery. Supports both VTU and Share and Sell
            airtime types.
          </Typography>
        </Card>

        {/* Endpoint */}
        <EndpointCard
          method="POST"
          endpoint={`${HOST_API}/api/topup`}
          description="Purchase airtime for specified phone number"
        >
          <ParameterTable parameters={parameters} title="Request Parameters" />

          <Box sx={{ mt: 3 }}>
            <Typography variant="subtitle2" gutterBottom>
              Code Example (PHP)
            </Typography>
            <CodeBlock code={requestCode} language="php" title="PHP" />
          </Box>

          <Box sx={{ mt: 3 }}>
            <Typography variant="subtitle2" gutterBottom>
              Success Response
            </Typography>
            <CodeBlock code={successResponse} language="json" title="JSON Response" />
          </Box>
        </EndpointCard>

        {/* Important Notes */}
        <Card sx={{ p: 3, mb: 3 }}>
          <Typography variant="h6" gutterBottom>
            Important Notes
          </Typography>
          <Stack spacing={2}>
            <Alert severity="info">
              <Typography variant="body2">
                <strong>Amount Range:</strong> Minimum â‚¦50, Maximum â‚¦50,000
              </Typography>
            </Alert>
            <Alert severity="warning">
              <Typography variant="body2">
                <strong>Airtime Types:</strong> VTU (recommended) or Share and Sell
              </Typography>
            </Alert>
            <Alert severity="success">
              <Typography variant="body2">
                <strong>Instant Delivery:</strong> Airtime is delivered instantly upon successful transaction.
              </Typography>
            </Alert>
          </Stack>
        </Card>

      </Container>
    </Page>
  );
}
